.clock {
  width: 4rem;
  height: 4rem;
  border: 10px solid rgb(5, 5, 5);
  border-radius: 50%;
  margin: 1px auto;
  position: relative;
  padding: 2rem;
  box-shadow: 0 0 0 4px rgba(0, 0, 0, 0.1), inset 0 0 0 3px #37000a,
    inset 0 0 10px black, 0 0 10px rgba(0, 0, 0, 0.2);
  background-color: white;
}

.clock-face {
  position: relative;
  width: 100%;
  height: 100%;
  transform: translateY(-3px);
}

.hour-hand {
  width: 50%;
  height: 4px;
  background: black;
  position: absolute;
  top: 50%;
  transform-origin: 100%;
  transform: rotate(250deg);
}

.min-hand {
  width: 50%;
  height: 2px;
  background: black;
  position: absolute;
  top: 50%;
  transform-origin: 100%;
  transform: rotate(95deg);
}

.sec-hand {
  width: 50%;
  height: 1px;
  background: black;
  position: absolute;
  top: 50%;
  transform-origin: 100%;
  transform: rotate(100deg);
}

